body {
  display:flex;
  flex-direction: column;
  align-items: center;
  background-color: hsl(0, 0%,95%);

}
.stopwatch{
  display:flex;
  flex-direction: column;
  align-items: center;
  border:5px solid;
  border-radius: 50px;
  background-color: white;
  padding: 30px;
   
} .display{
  font-size:5rem;
  font-family: monospace;
  font-weight: bold;
  margin-bottom:25px;
  color:hsl(0, 0%, 30%);
  text-shadow:2px 2px 2px hsl(0, 0%, 0%,0.75);


}
.controls button {
   font-size:1.5rem;
   font-weight:bold;
   padding:10px 20px;
   margin:5px;
   min-width:125px;
   border:none;
   border-radius:10px;
   cursor:pointer;
   color:white;
   transition:background-color 0.5ease;

}
.start-button{
  background-color: hsl(115,100%,40%);
}
.start-button:hover{
  background-color:hsl(115,100%,35%);
}
.stop-button{
  background-color: hsl(10,90%,50%);
}
.stop-button:hover{
  background-color: hsl(10,90%,45%);
}
.reset-button{
  background-color: hsl(205,90%,50%);
}
.reset-button:hover{
  background-color:hsl(205,90%,60%); 
}

     
